import request from '@/utils/request'
/**
 *
 * 
 *
 **/
// 
export const login = (data: any) =>
  request({
    'url': '/manage/login',
    'method': 'post',
    data: data
  })

// 
 export const adminLogout = (params: any) =>
 request({
   'url': `/manage/logout`,
   'method': 'post',
   params
 })

// 
export const getAdminList = (params: any) =>
  request({
    'url': `/manage/page`,
    'method': 'get',
    'params': params
  })

//  /
export const startOrStopAdmin = (params: any) =>
  request({
    'url': `/manage/status/${params.status}`,
    'method': 'post',
    'params': {id: params.id}
  })

//
export const addAdmin = (data: any) =>
  request({
    'url': '/manage',
    'method': 'post',
    data: data
  })

//id
export const getAdminById = (id: number) =>
  request({
    'url': `/manage/${id}`,
    'method': 'get'
  })

//
export const updateAdmin = (data: any) =>
  request({
    'url': '/manage',
    'method': 'put',
    data: data
  })
