import request from '@/utils/request'
/**
 *
 * @param data
 */
// 
export const editPassword = (data: any) =>
  request({
    'url': '/admin/editPassword',
    'method': 'put',
    data
  })
// 
export const getStatus = () =>
request({
    'url': `/uniapp/status`,
    'method': 'get'
  })
// 
export const setStatus = (data:any) =>
request({
      'url': `/uniapp/`+data,
      'method': 'put',
      'data':data
  })
